@tailwind base;
@tailwind components;
@tailwind utilities;

body{
    color :rgb(189, 194, 193);
background: linear-gradient(350deg, rgba(176,34,231,1) 0%, rgba(9,70,121,0.9612219887955182) 46%, rgba(125,56,184,0.9724264705882353) 100%);
background-attachment: fixed;
}

.shake {
    position: relative;
    animation: shake 400ms ease-out;
}

@keyframes shake {
    10%, 90% {
        transform: translate3d(-1px,0,0);
    }
    20%, 80% {
        -webkit-transform: translate3d(2px,0,0);
        transform: translate3d(2px,0,0);
    }
    30%, 50%, 70% {
        -webkit-transform: translate3d(-4px,0,0);
        transform: translate3d(-4px,0,0);
    }
    40%, 60% {
        -webkit-transform: translate3d(4px,0,0);
        transform: translate3d(4px,0,0);
    }
}

input::placeholder{
    position:relative;
    margin-left: 40px;
}
.logo-animation{
    transition: transform 400ms ease-in-out;
}
.logo-animation:hover{
    transform: rotateY(180deg);
}

::-webkit-scrollbar{
    width: 10px;
    background-color: rgba(9,70,121,0.9612219887955182);
    border-color:rgba(9,70,121,0.9612219887955182) ;
}

::-webkit-scrollbar-thumb{
    background-color: rgba(176,34,231,1);
    border-radius: 20px;
}

.loading{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    z-index: 40;
}
